:root {
  --color-dark-text: #494450;
  --color-text: #615c69;
  --color-light-text: #d2bea6;
  --font-bebas: 'Bebas Neue', Arial, Helvetica, sans-serif;
  --bg-body: #f1e2cf;
  --bg-light: #e8d7c3;
  --bg-dark: #494450;
  --transition: 0.2s ease-in-out;
}

@font-face {
  font-family: 'Bebas Neue';
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/BebasNeueBold.woff2) format('woff2'), url(../fonts/BebasNeueBold.woff) format('woff');
}

@font-face {
  font-family: 'Montserrat';
  font-weight: 900;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/Montserrat-Black.woff2) format('woff2'), url(../fonts/Montserrat-Black.woff) format('woff');
}

@font-face {
  font-family: 'Montserrat';
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/Montserrat-Medium.woff2) format('woff2'), url(../fonts/Montserrat-Medium.woff) format('woff');
}

html {
  height: 100%;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  font-weight: 500;
  font-size: 13px;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  color: var(--color-text);
  background-color: var(--bg-body);
}

abbr[title] {
  text-decoration: underline;
  text-decoration: underline dotted;
  border-bottom: none;
}

b,
strong {
  font-weight: bolder;
}

small {
  font-size: 80%;
}

img {
  max-width: 100%;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-size: 100%;
  line-height: 1.15;
  font-family: inherit;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  display: table;
  box-sizing: border-box;
  max-width: 100%;
  padding: 0;
  color: inherit;
  white-space: normal;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: textfield;
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

details {
  display: block;
}

summary {
  display: list-item;
}

.visually-hidden {
  position: absolute;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border: none;
}

.col {
  box-sizing: border-box;
  min-width: 320px;
  max-width: 1620px;
  padding-right: 30px;
  padding-left: 30px;
  margin-left: auto;
  margin-right: auto;
}

.page-content {
  width: 100%;
  max-width: 1100px;
}

.page-footer {
  --color-footer-text: #85818c;
  background-color: var(--bg-dark);
  color: var(--color-footer-text);
  font-size: 12px;
  line-height: 16px;
}

.page-footer__col {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}

.page-footer__page-content {
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.page-footer__social-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
}

.page-footer__social-item:not(:last-child) {
  margin-right: 15px;
}

.page-footer__social-link {
  display: flex;
  align-items: center;
  width: 20px;
  height: 20px;
}

.page-footer__social-link svg {
  fill: var(--color-footer-text);
}

.page-footer__social-link--instagram:hover svg {
  fill: url(#a);
}

.page-footer__social-link--vk svg .center {
  fill: var(--bg-dark);
}

.page-footer__social-link--vk:hover svg {
  fill: #2787f5;
}

.page-footer__social-link--vk:hover svg .center {
  fill: #fff;
}

.page-footer__social-link--youtube svg .center {
  fill: var(--bg-dark);
}

.page-footer__social-link--youtube:hover svg {
  fill: #ff0000;
}

.page-footer__social-link--youtube:hover svg .center {
  fill: #fff;
}

.page-footer__social-link--telegram svg .center {
  fill: var(--bg-dark);
}

.page-footer__social-link--telegram:hover svg {
  fill: url(#a1);
}

.page-footer__social-link--telegram:hover svg .center {
  fill: #fff;
}

.page-footer__social-link--facebook svg .center {
  fill: var(--bg-dark);
}

.page-footer__social-link--facebook:hover svg {
  fill: #1877f2;
}

.page-footer__social-link--facebook:hover svg .center {
  fill: #fff;
}

@media (min-width: 576px) {
  .hidde-min-sm {
    display: none;
  }
}

@media (min-width: 768px) {
  .hidde-min-md {
    display: none;
  }

  .col {
    padding-right: 40px;
    padding-left: 40px;
  }
}

@media (min-width: 990px) {
  .hidde-min-lg {
    display: none;
  }
}

@media (min-width: 1200px) {
  .hidde-min-xl {
    display: none;
  }
}

@media (min-width: 1620px) {
  .hidde-min-xxl {
    display: none;
  }
}

@media (max-width: 1619px) {
  .hidde-max-xxl {
    display: none;
  }
}

@media (max-width: 1199px) {
  .hidde-max-xl {
    display: none;
  }
}

@media (max-width: 989px) {
  .hidde-max-lg {
    display: none;
  }
}

@media (max-width: 767px) {
  .hidde-max-md {
    display: none;
  }
}

@media (max-width: 575px) {
  .hidde-max-sm {
    display: none;
  }
}